import { Player } from "@minecraft/server"

import type { CommandTarget } from "@/lib/commands/Commands.class"
import { Dialog } from "@/lib/dialog/index"
import { asyncRun, errorHandler } from "@/util/game"

import { Handler } from "./Handler.class"
import { option } from "./option"

export async function command(argv: string[], sender: CommandTarget) {
  if (sender instanceof Player) {
    const playerOption = option.getPlayer(sender)
    const handler = new Handler(sender)

    const objectiveId = argv[2]
    switch (argv[1]) {
      case "add": {
        const [, , , criteria, displayName] = argv
        if (!objectiveId) throw errorHandler("objectiveId ", sender)
        if (!criteria) throw errorHandler("criteria ", sender)

        if (!playerOption.getItemVal("enable_creative"))
          sender.sendMessage("")

        const result = await handler.add({ objectiveId, criteria, displayName })
        if (result.code)
          sender.sendMessage(
            ` "${displayName}" (${objectiveId})  "${criteria}"`
          )
        else {
          switch (result.message) {
            case "DUPLICATE_OBJECTIVE": {
              throw errorHandler(
                ` ${objectiveId} `,
                sender
              )
            }
            case "UNKNOWN_CRITERIA": {
              throw errorHandler(` ${criteria}`, sender)
            }
          }
        }

        if (playerOption.getItemVal("auto_start")) {
          const startResult = await handler.start({ objectiveId, criteria })
          if (startResult)
            sender.sendMessage(` ${displayName} `)
        }

        break
      }
      case "remove":
      case "rm":
      case "-r": {
        async function _remove() {
          const result = await handler.remove({ objectiveId })
          if (result) (sender as Player).sendMessage(` ${objectiveId}`)
          else
            throw errorHandler(
              ` ${objectiveId} `,
              sender as Player
            )
        }

        if (playerOption.getItemVal("enable_confirm_dialog"))
          await Dialog.confirm({
            body: ` ${objectiveId} `,
            target: sender,
            onConfirm: _remove,
          })
        else await _remove()

        break
      }
      case "stop": {
        const result = await handler.stop({ objectiveId })

        if (result) sender.sendMessage(` ${objectiveId} `)
        else
          throw errorHandler(
            ` ${objectiveId} `,
            sender
          )

        break
      }
      case "start": {
        const result = await handler.start({ objectiveId })

        if (result) sender.sendMessage(` ${objectiveId} `)
        else
          throw errorHandler(
            ` ${objectiveId} `,
            sender
          )

        break
      }
      case "option":
      case "opt":
      case "-o": {
        await asyncRun(() => playerOption.showDialog())
        break
      }
      default: {
        throw errorHandler(` ${argv[1]}`, sender)
      }
    }
  }

  throw new Error("Target must be a player.")
}
